var validatorStore = {
  // 
  required(val) {
    if (JSON.stringify(val) == "[]" || (!val && val !== 0)) return ''
  },
  maxLength(val, len) {
    if (val.length > len) return '' + len + ''
  },
  minLength(val, len) {
    if (val.length < len) return '' + len + ''
  },
  between(val, min, max) {
    if (val.length < min || val.length > max) return `${min}${max}`
  },
  equalTo(val, selector) { // input
    var targetEl = document.querySelector(selector)
    var tagName = targetEl.tagName.toLocaleLowerCase()
    targetEl = tagName == 'input' ? targetEl : targetEl.querySelector('input')
    if (val != targetEl.value) return ''
  },
  length(val, len) {
    if (len != val.length) return `${len}`
  },
  email(val) {
    var p = /^([\w\.\-]+)@([\w\.\-]+)\.([\w]{2,4})$/
    if (!p.test(val)) return ''
  },
  number(val) {
    if (!/^\d+$/.test(val)) return ''
  }
}

export default {
  getValidator(name) {
    if (!validatorStore[name]) throw `${name}`
    return validatorStore[name]
  },
  addValidator(name, validator) {
    validatorStore[name] = validator
  }
}



